<template>
    <div class="login">
        <h1>登录页</h1>
        <div>
            <input v-model="form.account" placeholder="请输入您的账号">
        </div>
        <div>
            <input v-model="form.password" placeholder="请输入您的密码">
        </div>
        <button @click="login">登录</button>
    </div>
</template>

<script>
import store from "../store"

export default {
    data() {
        return {
            form: {
                account: "",
                password: ""
            }
        }

    },
    created() {

    },
    mounted() {
    },
    methods: {
        login() {
            if (!this.form.account || !this.form.password) {
                alert("请填写账号密码")
                return false;
            }
            const that = this
            setTimeout(() => {
                store.commit('login', {
                    account: that.form.account,
                    password: that.form.password
                });
                store.commit('setMemberInfo', {
                    userStatus: 0,
                    vipLevel: 0
                })
                that.$router.push("./")
            }, 500)
        }
    }
}
</script>